.App {
  text-align: center;
}

html { /*overflow-y: hidden; */}

.header {
  background-color: #3870df;
  display: flex;
  /* align-items: center;*/
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  -webkit-text-stroke: 2px black;
  border-bottom: 5px solid black;
}

.feedback-button-correct {
  width: 200px;
  height: 50px;
  margin-right: 20px;
  background-color: rgb(0, 139, 0);
  color: white;
  display: inline;
  font-size: 18px;
}

  .feedback-button-correct:hover {
    width: 200px;
    height: 50px;
    margin-right: 20px;
    background-color: rgb(78, 255, 78);
    color: black;
    display: inline;
    font-size: 18px;
  }
  
  .feedback-button-incorrect {
    width: 200px;
    height: 50px;
    margin-right: 20px;
    background-color: rgb(148, 0, 0);
    color: white;
    display: inline;
    font-size: 18px;
  }

    .feedback-button-incorrect:hover{
      width: 200px;
      height: 50px;
      margin-right: 20px;
      background-color: rgb(255, 99, 99);
      color:black;
      display: inline;
      font-size: 18px;
    }

.body{
  /*background-color: #5d6370;*/
  background-image: linear-gradient(rgb(24, 198, 169), yellow);
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}

.footer{
  border-top: 5px solid black;
  min-height: 10vh;
  background-color: #3870df;
  margin-top: auto;
}

img {
  border: 5px solid black;
}

.App-link {
  
  color: #61dafb;
}
